.Footer {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: center;
    height: calc(100 / 1440 * 100vw);
    font-family: 'Andika';
    color: #97404C;
    font-style: normal;
    font-weight: 700;
    margin-top: 3%;
    margin-bottom: 1%;
}

.Footer-container {
    width: calc(750 / 1440 * 100vw);
    height: calc(100 / 1440 * 100vw);
    box-shadow: 0px 0px 4px 0px rgb(0 0 0 / 65%);
    border-radius: 3px;
    overflow: hidden;
    display: inline-flex;
    background: #ffffff;
}

.Footer-logo img{
    width: calc(50 / 1440 * 100vw);
    height: calc(50 / 1440 * 100vw);
    margin-left: 30%;
    margin-top: 30%;
}

.Footer-title {
    margin-left: 4%;
    width: calc(300 / 1440 * 100vw);
}

h3 {
    font-size: calc(30 / 1440 * 100vw);
    line-height: calc(40 / 1440 * 100vw);
}

.Footer-text {
    width: calc(400 / 1440 * 100vw);
    font-size: calc(16 / 1440 * 100vw);
    margin-left: -10%;
}

.Footer-subscription {
    display: flex;
    flex-direction: column;
    width: calc(300 / 1440 * 100vw);
    height: calc(100 / 1440 * 100vw);
    box-shadow: 0px 0px 4px 0px rgb(0 0 0 / 65%);
    border-radius: 3px;
    overflow: hidden;
    margin-left: 10%;
    background: #ffffff;
}

h4 {
    width: calc(250 / 1440 * 100vw);;
    font-size: calc(18 / 1440 * 100vw);
    line-height: calc(40 / 1440 * 100vw);
    margin-top: 0.5%;
    margin-left: 14%;
}

.Footer-links {
    display: flex;
    flex-direction: column;
    margin-top: -11%;
    margin-left: 40%;
    font-size: calc(16 / 1440 * 100vw);
    line-height: calc(18 / 1440 * 100vw);
}